.header {
    display: flex;
    align-items: center;
    width: 100%;
  }
.links {
    display:flex;
    align-items: center;
    flex-direction: column;
    width: 100%;
  }

  .logo-image{
    width: 15vw;
    padding-left: 15%;
  }

  ul {
    list-style-type: none;
    display: flex;
    justify-content: center;
    flex: 1;
    flex-wrap: wrap;
    padding-inline-start: 0;
  }

  .logotext {
    display:flex;
    flex-direction: column;
    align-items: center;
    
  }

  .navline{
    min-width: 100vw;
    padding-block: 2px;
    background: #aa2e2d;
  }

  a {
    display: inline-block;
    padding: 8px 16px;
    font-size: 2.5vw;
    font-weight: bold;
    text-decoration: none;
    color: #000000;
  }
  a:hover {
    color: #aa2e2d;
  }
  a:active {
    color: #aa2e2d;
  }

  @media only screen and (min-width: 850px){
    .logo-image {
      max-height: 150px;
      max-width: 150px;
    }
    h1 {
      font-size: 25.5px;
    }
    a {
      font-size: 21.5px;
    }
  }